import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import All from './Content/All';
import Good from './Content/Good';
import Share from './Content/Share';
import Ask from './Content/Ask';
import Job from './Content/Job';
import Xiangqing from './Content/Xiangqing'
import Content from './Content/Content';
import Contentgood from './Content/Contentgood';

export default class Home extends Component {
    render() {
        let { url } = this.props.match
        // console.log(this.props);
        console.log(url);
        return (
            <div>
                <div>
                    {/* <Link to={`${url}/tab=all`}>全部</Link> */}
                    <Link to='/home/all/1'>全部</Link>
                    <Link to='/home/good/1'>精华</Link>
                    <Link to='/home/share/1'>分享</Link>
                    <Link to='/home/ask/1'>问答</Link>
                    <Link to='/home/job/1'>招聘</Link>
                </div>
                <div>
                    <Route path='/home/all' component={All} />
                    <Route path='/home/good' component={Good} />
                    <Route path='/home/share' component={Share} />
                    <Route path='/home/ask' component={Ask} />
                    <Route path='/home/job' component={Job} />
                </div>
                <Route exact path="/home/content/:xiangqing" component={Xiangqing} />
            </div>
        )
    }
}
