import React, { Component } from 'react'
import {Link} from 'react-router-dom';

export default class Signin extends Component {
    render() {
        return (
            <div>
                <div className="head">主页/登录</div>
                <div className="inner">
                    <div>
                        <p>用户名</p>
                        <input type="text" />
                    </div>
                    <div>
                        <p>密码</p>
                        <input type="password" />
                    </div>
                    <Link to="/home/all/1">
                        <button type="primary" className="onhome">Github登录</button>
                    </Link>
                </div>
            </div>
        )
    }
}


