import React from 'react';
import {useHistory} from 'react-router-dom';
import {Link} from 'react-router-dom';


export default class  Content extends React.Component{
    constructor(){
        super();
        this.state={
            data:[]
        }
    }

    componentDidMount(){
        console.log(this.props);
        let page=this.props.match.params.id;
        fetch('https://cnodejs.org/api/v1/topics/?tab=ask&page='+page)
        .then((res)=>res.json())
        .then((res)=>{
            console.log(res);
            this.setState({
                data:res.data
            },()=>{console.log(this.state.data)})
        })
        console.log(this.state.data);
    }
    componentDidUpdate(prevProps,prevState){
        if(prevProps.match.params.id!==this.props.match.params.id){
            let page=this.props.match.params.id;
            fetch('https://cnodejs.org/api/v1/topics/?tab=ask&page='+page)
            .then((res)=>res.json())
            .then((res)=>{
                this.setState({
                    data:res.data
                })
            })
        }
    }
    render(){
        let { url } = this.props.match
        console.log(url);
        return (
            <div>
                {
                    this.state.data.map((item)=>(
                        <div key={item.id} style={{width:'800px',height:'50px',border:"1px solid red",clear:"both"}}>

                            <img src={item.author.avatar_url} style={{height:'30px',width:'30px',float:'left'}}/>

                            <div style={{float:'left'}}>{item.reply_count}/{item.visit_count}</div>

                            <div style={{backgroundColor:'#e5e5e5',color: '#999',float:'left'}}>问答</div>

                            <Link style={{fontSize:'14px',lineHeight:'30px',float:'left',textOverflow:'ellipsis',width:'650px',
                            height:'25px'}} to={{pathname:'/home/content/'+item.id,state:{content:item.content}}}>{item.title}</Link>

                            <img src={item.author.avatar_url} style={{height:'20px',width:'20px',float:'right'}}/>

                            <div style={{float:'right'}}>1天前</div>
                            {/* <div dangerouslySetInnerHTML={{__html:item.content}}></div> */}
                        </div>
                    ))
                }
            </div>
        )
    }
}